@import url("https://fonts.googleapis.com/css2?family=Lato&display=swap");*{box-sizing:border-box}body{font-family:'Lato', sans-serif;transition:transform 0.3s ease;margin:0;padding:0}:root{--modal-duration: .5s;--primary-color: #30336b;--secondary-color: #be2edd}nav{background-color:var(--primary-color);border-right:2px solid rgba(200,200,200,0.1);color:white;position:fixed;top:0;left:0;width:200px;height:100vh;z-index:100;transform:translateX(-100%)}nav .logo{padding:30px 0;text-align:center}nav .logo img{height:75px;width:75px;border-radius:50%}nav ul{padding:0;list-style-type:none;margin:0}nav ul li{border-bottom:2px solid rgba(200,200,200,0.1);padding:20px}nav ul li a{color:white;text-decoration:none}nav ul li a:hover{text-decoration:underline}nav ul li:first-of-type{border-top:2px solid rgba(200,200,200,0.1);padding:20px}body.show-nav{transform:translateX(200px)}header{background-color:var(--primary-color);color:white;font-size:130%;position:relative;padding:40px 15px;text-align:center}header h1{margin:0}header p{margin:30px 0}button,input[type='submit']{background-color:var(--secondary-color);border:0;border-radius:5px;color:white;cursor:pointer;padding:8px 12px}button:focus{outline:none}.toggle{background-color:rgba(0,0,0,0.3);position:absolute;top:20px;left:20px}.cta-btn{padding:12px 30px;font-size:20px}.container{padding:15px;margin:0 auto;max-width:100%;width:800px}.modal-container{background-color:rgba(0,0,0,0.6);display:none;position:fixed;top:0;left:0;right:0;bottom:0}.modal-container.show-modal{display:block}.modal-container .modal{background-color:white;border-radius:5px;box-shadow:0 0 10px rgba(0,0,0,0.3);position:absolute;overflow:hidden;top:50%;left:50%;transform:translate(-50%, -50%);max-width:100%;max-height:100%;width:400px;animation-name:modalopen;animation-duration:var(--modal-duration)}.modal-container .modal .modal-header{background-color:var(--primary-color);color:#fff;padding:15px}.modal-container .modal .modal-header h3{margin:0;border-bottom:1px solid #333}.modal-container .modal-content{padding:20px}.modal-container .modal-form div{margin:15px 0}.modal-container .modal-form label{display:block;margin-bottom:5px}.modal-container .modal-form .form-input{padding:8px;width:100%}.modal-container .close-btn{background:transparent;font-size:25px;position:absolute;top:0;right:0}@keyframes modalopen{from{opacity:0}to{opacity:1}}
